import Observable from '../framework/observable';
export default class DestinationsModel extends Observable {
    #service;
    #destinations;
    constructor(service) {
        super();
        this.#service = service;
        this.#destinations = this.#service.destinations;
    }
    get destinations() {
        return this.#destinations;
    }
    get allDestinationsNames() {
        this.#destinations = this.#service.destinations;
        return Array.from(this.#destinations.map((waypoint) => waypoint.name));
    }
    get allDestinationsIDs() {
        this.#destinations = this.#service.destinations;
        return Array.from(this.#destinations.map((waypoint) => waypoint.id));
    }
    getDestinationByID = (destination) => this.#destinations.find((item) => item.id === destination);
    getDestinationByName = (name) => this.#destinations.find((item) => item.name === name);
}
