const equalWaypoints = (waypointA, waypointB) => {
    const pointA = { ...waypointA };
    const pointB = { ...waypointB };
    pointA.id = '0';
    pointB.id = '0';
    const pointAString = JSON.stringify(pointA);
    const pointBString = JSON.stringify(pointB);
    return pointAString !== pointBString;
};
export const isSameWaypoints = (waypoints, waypointB) => waypoints.every((waypoint) => equalWaypoints(waypoint, waypointB));
