import { render, replace, remove } from '../framework/render';
import FilterView from '../view/header/filter-view';
import { filter } from '../utils/filter';
import { FILTER_TYPES } from '../const';
export default class FilterPresenter {
    #filterContainer = null;
    #filterModel = null;
    #waypointsModel = null;
    #filterComponent = null;
    constructor({ filterContainer, filterModel, waypointsModel, }) {
        this.#filterContainer = filterContainer;
        this.#filterModel = filterModel;
        this.#waypointsModel = waypointsModel;
        this.#waypointsModel.addObserver(this.#handleModelEvent);
        this.#filterModel.addObserver(this.#handleModelEvent);
    }
    get filters() {
        const waypoints = this.#waypointsModel.waypoints;
        return Object.values(FILTER_TYPES).map((type) => ({
            type,
            count: filter[type](waypoints).length,
        }));
    }
    init() {
        const filters = this.filters;
        const prevFilterComponent = this.#filterComponent;
        this.#filterComponent = new FilterView({
            filters,
            currentFilterType: this.#filterModel.filter,
            onFilterTypeChange: this.#handleFilterTypeChange,
        });
        if (prevFilterComponent === null) {
            render(this.#filterComponent, this.#filterContainer);
            return;
        }
        replace(this.#filterComponent, prevFilterComponent);
        remove(prevFilterComponent);
    }
    #handleModelEvent = () => {
        this.init();
    };
    #handleFilterTypeChange = (filterType) => {
        if (this.#filterModel.filter === filterType) {
            return;
        }
        this.#filterModel.setFilter(0 /* UpdateType.MAJOR */, filterType);
    };
}
