import dayjs from 'dayjs';
const priceSort = (waypointA, waypointB) => waypointB.basePrice - waypointA.basePrice;
const getDuration = ({ dateFrom, dateTo }) => dayjs(dateTo).diff(dayjs(dateFrom));
const timeSort = (waypointA, waypointB) => getDuration(waypointB) - getDuration(waypointA);
const daySort = (waypointA, waypointB) => dayjs(waypointA.dateFrom).diff(dayjs(waypointB.dateFrom));
export { priceSort, timeSort, daySort };
