/* eslint-disable @typescript-eslint/no-explicit-any */
import EditWaypointFormView from '../view/main/edit-waypoint-form-view';
import WaypointView from '../view/main/waypoint-view';
import { render, replace, remove } from '../framework/render';
import { isDatesEqual } from '../utils/time';
import dayjs from 'dayjs';
export default class WaypointPresenter {
    #mainListContainer;
    #waypointComponent = null;
    #waypointEditComponent = null;
    #dataBase = null;
    #waypoint = null;
    #handleDataChange;
    #handleModeChange;
    #mode = 0 /* Mode.DEFAULT */;
    constructor({ mainListContainer, onDataChange, onModeChange, }) {
        this.#mainListContainer = mainListContainer;
        this.#handleDataChange = onDataChange;
        this.#handleModeChange = onModeChange;
    }
    init({ waypoint, dataBase }) {
        this.#dataBase = dataBase;
        this.#waypoint = waypoint;
        const prevWaypointComponent = this.#waypointComponent;
        const prevWaypointEditComponent = this.#waypointEditComponent;
        this.#waypointComponent = new WaypointView({
            waypoint: this.#waypoint,
            dataBase: this.#dataBase,
            onEditClick: this.#handleEditClick,
            onFavoriteClick: this.#handleFavoriteClick,
        });
        this.#waypointEditComponent = new EditWaypointFormView({
            waypoint: this.#waypoint,
            dataBase: this.#dataBase,
            onFormSubmit: this.#handleFormSubmit,
            onDeleteClick: this.#handleDeleteClick,
        });
        if (prevWaypointComponent === null || prevWaypointEditComponent === null) {
            render(this.#waypointComponent, this.#mainListContainer);
            return;
        }
        if (this.#mode === 0 /* Mode.DEFAULT */) {
            replace(this.#waypointComponent, prevWaypointComponent);
        }
        if (this.#mode === 1 /* Mode.EDITING */) {
            replace(this.#waypointEditComponent, prevWaypointEditComponent);
        }
        remove(prevWaypointComponent);
        remove(prevWaypointEditComponent);
    }
    destroy() {
        remove(this.#waypointComponent);
        remove(this.#waypointEditComponent);
    }
    resetView() {
        if (this.#mode !== 0 /* Mode.DEFAULT */) {
            this.#onDeleteClick();
        }
    }
    #switchToEditMode() {
        if (!this.#waypointComponent || !this.#waypointEditComponent) {
            return;
        }
        replace(this.#waypointEditComponent, this.#waypointComponent);
        document.addEventListener('keydown', this.#escKeyDownHandler);
        this.#handleModeChange();
        this.#mode = 1 /* Mode.EDITING */;
    }
    #switchToViewMode() {
        if (!this.#waypointComponent || !this.#waypointEditComponent) {
            return;
        }
        this.#waypointEditComponent.reset(this.#waypoint);
        replace(this.#waypointComponent, this.#waypointEditComponent);
        document.removeEventListener('keydown', this.#escKeyDownHandler);
        this.#mode = 0 /* Mode.DEFAULT */;
    }
    #escKeyDownHandler = (evt) => {
        if (evt.key === 'Escape') {
            evt.preventDefault();
            this.#waypointEditComponent.reset(this.#waypoint);
            this.#switchToViewMode();
        }
    };
    #handleFavoriteClick = () => {
        if (this.#waypoint) {
            this.#handleDataChange(0 /* UserAction.updateWaypoint */, 1 /* UpdateType.MINOR */, { ...this.#waypoint, isFavorite: !this.#waypoint.isFavorite });
        }
    };
    #handleEditClick = () => this.#switchToEditMode();
    #onDeleteClick = () => this.#switchToViewMode();
    #handleFormSubmit = (updateWaypoint) => {
        const isMinorUpdate = !isDatesEqual(dayjs(this.#waypoint.dateFrom), dayjs(updateWaypoint.dateFrom)) ||
            !isDatesEqual(dayjs(this.#waypoint.dateTo), dayjs(updateWaypoint.dateTo)) ||
            this.#waypoint?.offers !== updateWaypoint.offers;
        this.#handleDataChange(0 /* UserAction.updateWaypoint */, isMinorUpdate ? 1 /* UpdateType.MINOR */ : 2 /* UpdateType.PATCH */, updateWaypoint);
        this.#switchToViewMode();
    };
    #handleDeleteClick = (waypoint) => {
        this.#handleDataChange(2 /* UserAction.deleteWaypoint */, 1 /* UpdateType.MINOR */, waypoint);
    };
}
