import { render, remove } from '../framework/render';
import NewWaypointFormView from '../view/main/new-waypoint-form-view';
export default class NewWaypointPresenter {
    #mainListContainer;
    #handleDataChange;
    #handleDestroy;
    #waypointNewComponent = null;
    #dataBase;
    #waypoint;
    constructor({ mainListContainer, waypoint, dataBase, onDataChange, onDestroy, }) {
        this.#mainListContainer = mainListContainer;
        this.#dataBase = dataBase;
        this.#waypoint = waypoint;
        this.#handleDataChange = onDataChange;
        this.#handleDestroy = onDestroy;
    }
    init() {
        if (this.#waypointNewComponent !== null) {
            return;
        }
        this.#waypointNewComponent = new NewWaypointFormView({
            waypoint: this.#waypoint,
            dataBase: this.#dataBase,
            onFormSubmit: this.#handleFormSubmit,
            onDeleteClick: this.#handleDeleteClick,
        });
        render(this.#waypointNewComponent, this.#mainListContainer, 'afterbegin');
        document.addEventListener('keydown', this.#escKeyDownHandler);
    }
    destroy() {
        if (this.#waypointNewComponent === null) {
            return;
        }
        this.#handleDestroy();
        remove(this.#waypointNewComponent);
        this.#waypointNewComponent = null;
        document.removeEventListener('keydown', this.#escKeyDownHandler);
    }
    #handleFormSubmit = (newWaypoint) => {
        this.#handleDataChange(1 /* UserAction.addWaypoint */, 1 /* UpdateType.MINOR */, { ...newWaypoint });
        this.destroy();
    };
    #handleDeleteClick = () => {
        this.destroy();
    };
    #escKeyDownHandler = (evt) => {
        if (evt.key === 'Escape') {
            evt.preventDefault();
            this.destroy();
        }
    };
}
